import Map from "@arcgis/core/Map";
import MapView from "@arcgis/core/views/MapView";
import WMTSLayer from "@arcgis/core/layers/WMTSLayer";
import Point from "@arcgis/core/geometry/Point";
import SpatialReference from "@arcgis/core/geometry/SpatialReference";
import TileInfo from "@arcgis/core/layers/support/TileInfo";
import Extent from "@arcgis/core/geometry/Extent";

import esriConfig from "@arcgis/core/config";
esriConfig.assetsPath = "https://js.arcgis.com/4.22/@arcgis/core/assets"; // à mettre dans la config

import { MapControlParams } from "./MapControlParams";
export type { MapControlParams } from "./MapControlParams";

import "@arcgis/core/assets/esri/themes/light/main.css";

export class MapControl {
    private view: MapView;

    /**
     * @param  {MapControlParams} params
     */
    public constructor(params: MapControlParams) {
        this.build(params);
    }
    /**
     * Centre la carte sur un nouvel emplacement
     * @param  {number} x Coordonnée X du nouveau centre
     * @param  {number} y Coordonnée Y du nouveau centre
     * @param  {number} scale Echelle
     * @returns void
     */
    public center(x: number, y: number, scale?: number): void {
        this.view.center = new Point({
            x,
            y,
            spatialReference: this.view.spatialReference
        });
        if (scale !== undefined) {   
            this.view.scale = scale;
        }
    }

    private build(params: MapControlParams): void {
        const wmts = new WMTSLayer({
            url: "https://wmts.asit-asso.ch/wmts/1.0.0/",
            activeLayer: {
                id: "asitvd.fond_couleur"
            }
        });
        const map = new Map({
            layers: [wmts],
        });
        const spatialReference = new SpatialReference({
            wkid: 2056
        });

        let center: Point;
        if (params.center === undefined){
            center = new Point({
                x: 2530000,
                y: 1160000,
                spatialReference
            });
        } else {
            center = new Point({
                x: params.center[0],
                y: params.center[1],
                spatialReference
            });
        }
        
        // diminuer le nombre de valeurs pour ne pas devoir limiter avec minScale
        const scales = [15118110.236220477, 14173228.346456695, 13228346.456692917, 12283464.566929135, 11338582.677165356, 10393700.787401576, 9448818.897637796, 8503937.007874018, 7559055.118110239, 6614173.228346459, 5669291.338582678, 4724409.448818898, 3779527.5590551193, 2834645.669291339, 2456692.9133858276, 1889763.7795275596, 944881.8897637798, 377952.7559055118, 188976.3779527559, 75590.55118110238, 37795.27559055119, 18897.637795275594, 9448.818897637797, 7559.055118110236, 5669.291338582678, 3779.527559055118, 1889.763779527559, 944.8818897637796, 377.9527559055119, 188.97637795275594];
        const tileInfo = TileInfo.create({
            spatialReference,
            numLODs: scales.length,
            scales
        });
        const extent = new Extent({
            xmin: 2480000,
            ymin: 1110000,
            xmax: 2595000,
            ymax: 1208000,
            spatialReference
        });
        this.view = new MapView({
            container: params.container,
            map,
            scale: params.scale === undefined ? 300000 : params.scale,
            center,
            spatialReference,
            constraints: {
                rotationEnabled: false,
                lods: tileInfo.lods,
                minScale: 1000000,
                geometry: extent
            }
        });

    }
}
